@import url(https://fonts.googleapis.com/css2?family=News+Cycle&display=swap);
body {
  margin: 0;
  background-color: #5f5f5f;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h3 {
  padding: 0; margin: 0;
}

.header {
  margin-top: 1vw;
  text-align: center;
  background-color: #222222BC;
  padding-bottom: 5px;
  border-top: 2px solid #777;
  border-bottom: 2px solid #777;
  box-shadow: 0px 5px 8px #111;
}

@media screen and (max-width: 767px) {
  .header {
    padding-bottom: 5px;
  }
  .header-text {
    font-size: 32px;
  }
}

@media screen and (min-width: 768px) {
  .header {
    padding-bottom: 8px;
  }
  .header-text {
    font-size: 44px;
  }
}

@media screen and (min-width: 1024px) {
  .header {
    padding-bottom: 12px;
  }
  .header-text {
    font-size: 50px;
  }
}

.header-text {
  font-family: "News Cycle", sans-serif;
  color: #bebebe;
}
.card {
  border: 0px solid;
  border-radius: 15px;
  cursor: pointer;
  background-size: cover;
  background-position: center;
  padding-bottom: 38.25%;
  color: #cfcfcf;
  -webkit-animation: cardUnHover 0.2s forwards;
          animation: cardUnHover 0.2s forwards;
  overflow: hidden;
}

.card:before {
  content: "";
  position: absolute;
  top: -20px;
  left: -20px;
  bottom: -20px;
  right: -20px;
  z-index: -1;
  display: block;
  background: inherit;
  -webkit-filter: blur(6px);
          filter: blur(6px);
}

.card:hover {
  -webkit-animation: cardHover 0.2s forwards;
          animation: cardHover 0.2s forwards;
}

@-webkit-keyframes cardHover {
  from {
    transform: scale(1);
    box-shadow: 0px 5px 5px #111;
  }
  to {
    transform: scale(1.02);
    box-shadow: 5px 10px 10px #333;
  }
}

@keyframes cardHover {
  from {
    transform: scale(1);
    box-shadow: 0px 5px 5px #111;
  }
  to {
    transform: scale(1.02);
    box-shadow: 5px 10px 10px #333;
  }
}

@-webkit-keyframes cardUnHover {
  from {
    transform: scale(1.02);
    box-shadow: 5px 10px 10px #333;
  }
  to {
    transform: scale(1);
    box-shadow: 0px 5px 5px #111;
  }
}

@keyframes cardUnHover {
  from {
    transform: scale(1.02);
    box-shadow: 5px 10px 10px #333;
  }
  to {
    transform: scale(1);
    box-shadow: 0px 5px 5px #111;
  }
}

.card-content {
  padding-left: 10px;
  padding-top: 10px;
}

.card-title {
  border-radius: 3px;
  padding-left: 8px;
  padding-right: 8px;
  background-color: #575757a6;
  font-size: 32px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.card-description {
  padding-top: 15px;
  padding-left: 7px;
}

.card-description-text {
  border-radius: 2px;
  padding-left: 4px;
  padding-right: 4px;
  background-color: #575757a6;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  padding-top: 3px;
  padding-bottom: 3px;
  width: 100%;
  background-color: #222222BC;
  border-top: 2px solid #777;
  text-align: center;
}

.footer-text {
  font-family: "News Cycle", sans-serif;
  color: #666666;
  font-weight: bold;
}
