.card {
  border: 0px solid;
  border-radius: 15px;
  cursor: pointer;
  background-size: cover;
  background-position: center;
  padding-bottom: 38.25%;
  color: #cfcfcf;
  animation: cardUnHover 0.2s forwards;
  overflow: hidden;
}

.card:before {
  content: "";
  position: absolute;
  top: -20px;
  left: -20px;
  bottom: -20px;
  right: -20px;
  z-index: -1;
  display: block;
  background: inherit;
  filter: blur(6px);
}

.card:hover {
  animation: cardHover 0.2s forwards;
}

@keyframes cardHover {
  from {
    transform: scale(1);
    box-shadow: 0px 5px 5px #111;
  }
  to {
    transform: scale(1.02);
    box-shadow: 5px 10px 10px #333;
  }
}

@keyframes cardUnHover {
  from {
    transform: scale(1.02);
    box-shadow: 5px 10px 10px #333;
  }
  to {
    transform: scale(1);
    box-shadow: 0px 5px 5px #111;
  }
}

.card-content {
  padding-left: 10px;
  padding-top: 10px;
}

.card-title {
  border-radius: 3px;
  padding-left: 8px;
  padding-right: 8px;
  background-color: #575757a6;
  font-size: 32px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.card-description {
  padding-top: 15px;
  padding-left: 7px;
}

.card-description-text {
  border-radius: 2px;
  padding-left: 4px;
  padding-right: 4px;
  background-color: #575757a6;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}